
'use client';

import type { Building } from '@/types';
import { useState, useEffect, useMemo } from 'react';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Search, Package, CalendarDays } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/card';

const BUILDING_SIZES = ['小型', '中型', '大型'] as const;
const ALL_OPTIONS_VALUE = "__ALL_OPTIONS__"; // Unique value for "All" options

interface WorksFilterControlsProps {
  initialWorks: Building[];
  onFilteredWorks: (filteredWorks: Building[]) => void;
  availableYears?: number[];
  showYearFilter?: boolean;
  showSizeFilter?: boolean;
}

export default function WorksFilterControls({
  initialWorks,
  onFilteredWorks,
  availableYears: providedAvailableYears,
  showYearFilter = true,
  showSizeFilter = true,
}: WorksFilterControlsProps) {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSize, setSelectedSize] = useState<string>(''); // '' means all
  const [selectedYear, setSelectedYear] = useState<string>(''); // '' means all

  const availableYears = useMemo(() => {
    if (!showYearFilter) return [];
    if (providedAvailableYears) return providedAvailableYears.sort((a, b) => b - a);
    if (!initialWorks || initialWorks.length === 0) return [];
    const years = new Set(initialWorks.map(work => work.eventYear).filter(year => year != null));
    return Array.from(years).sort((a, b) => b - a);
  }, [initialWorks, providedAvailableYears, showYearFilter]);

  useEffect(() => {
    let filtered = [...initialWorks];

    if (searchTerm.trim()) {
      const lowerSearchTerm = searchTerm.toLowerCase();
      filtered = filtered.filter(
        (work) =>
          work.name.toLowerCase().includes(lowerSearchTerm) ||
          work.author.toLowerCase().includes(lowerSearchTerm) ||
          (work.description && work.description.toLowerCase().includes(lowerSearchTerm))
      );
    }

    if (showSizeFilter && selectedSize) { // selectedSize will be '' for "all"
      filtered = filtered.filter((work) => work.size === selectedSize);
    }

    if (showYearFilter && selectedYear) { // selectedYear will be '' for "all"
      filtered = filtered.filter((work) => work.eventYear === parseInt(selectedYear, 10));
    }

    onFilteredWorks(filtered);
  }, [searchTerm, selectedSize, selectedYear, initialWorks, onFilteredWorks, showSizeFilter, showYearFilter]);

  return (
    <Card className="mb-6 shadow-sm border-border/30 bg-card/50">
      <CardContent className="p-3 sm:p-4 space-y-3 md:space-y-0 md:flex md:flex-wrap md:items-end md:gap-3">
        <div className="flex-grow min-w-[180px] md:max-w-sm">
          <Label htmlFor="search-works" className="text-xs font-medium text-muted-foreground flex items-center mb-1">
            <Search className="mr-1.5 h-3.5 w-3.5" />
            搜索作品
          </Label>
          <Input
            id="search-works"
            type="text"
            placeholder="名称、作者、描述..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="h-9 text-sm"
          />
        </div>

        {showSizeFilter && (
          <div className="min-w-[120px] sm:min-w-[140px]">
            <Label htmlFor="filter-size" className="text-xs font-medium text-muted-foreground flex items-center mb-1">
              <Package className="mr-1.5 h-3.5 w-3.5" />
              规格
            </Label>
            <Select
              value={selectedSize || ALL_OPTIONS_VALUE} // Display "All Sizes" if selectedSize is ''
              onValueChange={(value) => {
                setSelectedSize(value === ALL_OPTIONS_VALUE ? '' : value);
              }}
            >
              <SelectTrigger id="filter-size" className="h-9 text-sm">
                <SelectValue placeholder="所有规格" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_OPTIONS_VALUE}>所有规格</SelectItem>
                {BUILDING_SIZES.map((s) => (
                  <SelectItem key={s} value={s}>
                    {s}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}

        {showYearFilter && availableYears.length > 0 && (
          <div className="min-w-[120px] sm:min-w-[140px]">
            <Label htmlFor="filter-year" className="text-xs font-medium text-muted-foreground flex items-center mb-1">
              <CalendarDays className="mr-1.5 h-3.5 w-3.5" />
              年份
            </Label>
            <Select
              value={selectedYear || ALL_OPTIONS_VALUE} // Display "All Years" if selectedYear is ''
              onValueChange={(value) => {
                setSelectedYear(value === ALL_OPTIONS_VALUE ? '' : value);
              }}
            >
              <SelectTrigger id="filter-year" className="h-9 text-sm">
                <SelectValue placeholder="所有年份" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_OPTIONS_VALUE}>所有年份</SelectItem>
                {availableYears.map((year) => (
                  <SelectItem key={year} value={String(year)}>
                    {year}年
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
