
'use client';

import type { Building, User } from '@/types';
import { useState, useMemo } from 'react';
import BuildingCard from '@/components/minecraft/BuildingCard';
import WorksFilterControls from '@/components/filters/WorksFilterControls';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Inbox, PlusSquare } from 'lucide-react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';

interface UserWorksDisplayProps {
  initialWorks: Building[];
  currentUser: User | null;
}

export default function UserWorksDisplay({
  initialWorks,
  currentUser,
}: UserWorksDisplayProps) {
  const [displayedWorks, setDisplayedWorks] = useState<Building[]>(initialWorks);

  const availableYearsForUserWorks = useMemo(() => {
    if (!initialWorks || initialWorks.length === 0) return [];
    const years = new Set(initialWorks.map(work => work.eventYear).filter(year => year != null));
    return Array.from(years).sort((a,b) => b - a);
  }, [initialWorks]);

  return (
    <div>
      {initialWorks.length > 0 && (
        <WorksFilterControls
          initialWorks={initialWorks}
          onFilteredWorks={setDisplayedWorks}
          availableYears={availableYearsForUserWorks}
          showYearFilter={availableYearsForUserWorks.length > 0} // Show year filter if user has works from different years
        />
      )}
      {displayedWorks.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {displayedWorks.map((building) => (
            <BuildingCard key={building.id} building={building} currentUser={currentUser} />
          ))}
        </div>
      ) : (
        <Alert className="border-primary/30 bg-primary/5">
          <Inbox className="h-5 w-5 text-primary" />
          <AlertTitle className="text-primary/90">
            {initialWorks.length > 0 ? '没有匹配的作品' : '暂无作品'}
          </AlertTitle>
          <AlertDescription className="text-primary/80">
            {initialWorks.length > 0 ? '请尝试调整搜索或筛选条件。' : (
              <>
                您还没有提交任何建筑作品。
                <Link href="/dashboard/submit" className="block mt-3">
                  <Button variant="outline" size="sm">
                    <PlusSquare className="mr-2 h-4 w-4" />
                    立即提交第一个作品
                  </Button>
                </Link>
              </>
            )}
          </AlertDescription>
        </Alert>
      )}
    </div>
  );
}
